//
// Contact
// --------------------------------------------------

.contact {
	.gray {
		padding-top: 90px;
		background-color: #F7F7F7;
	}
	h1 {
    	font-size: 40px;
    	line-height: 52px;
    	color: #434242;
    	font-weight: lighter;
    	margin: 0;
  	}
  	h2 {
  		font-size: 30px;
  		color: #555454;
  		font-weight: lighter;
  		margin: 10px 0 30px;
  	}
  	p {
  		font-size: 18px;
  		margin-bottom: 5px;
  		color: #434242;
  	}
  	form.contact {
  		.form-group {
  			margin-bottom: 7px;
  		}
  		#name, #email {
  			input {
				border-radius: 0;
  				border: 1px solid #979797;
  				height: 42px;
  			}
  		}
  		textarea {
  			border-radius: 0;
  			border: 1px solid #979797;
  		}
  		.btn {
  			display: block;
  			width: 100%;
  			border-radius: 0;
  			padding: 8px 0;
  			margin-bottom: 40px;
  		}
  	}
  	.col-md-4 {
  		img {
  			width: 100%;
  		}
  	}
    .location {
      h2 {
        margin: 10px 0;
      }
      p {
        margin-bottom: 0;
      }
    }
}